
const AdminLayout = ({ children }) => {
  return (
    <div className="flex min-h-screen bg-gray-100">
      <aside className="w-64 bg-white border-r hidden md:block">
        <div className="p-4 font-bold text-xl">Threadix Admin</div>
        <ul className="p-4 space-y-3 text-gray-700">
          <li className="hover:text-black cursor-pointer">Dashboard</li>
          <li className="hover:text-black cursor-pointer">Products</li>
          <li className="hover:text-black cursor-pointer">Orders</li>
          <li className="hover:text-black cursor-pointer">Users</li>
          <li className="hover:text-black cursor-pointer">Categories</li>
          <li className="hover:text-black cursor-pointer">Coupons</li>
        </ul>
      </aside>
      {children}
    </div>
  )
}

export default AdminLayout;
